.videoFooter{
    position: relative;
    bottom: 20%;
    color: #FFF;
    margin-left: 30px;
}


.videoFooter__text h3 {
    padding-bottom: 20px;
}
.videoFooter__text p {
    padding-bottom: 20px;
}

.videoFooter__music {
    display: flex;
}

.videoFootermusic__text {
    width: 80%;
    overflow-x: hidden;
}

.videoFootermusic__text p {
    animation: moveTheText 3s infinite linear;
}

.videoFooter__record{
    animation: spinTheRecord 3s infinite linear;
    position: absolute;
    bottom: 5px;
    right: 20px;
    height: 50px;
    filter: invert(1);
}

@keyframes spinTheRecord {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
@keyframes moveTheText {
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX(-100%);
    }
}

@media only screen and (max-width:470px) {
    .videoFooter {
        bottom: 22%;
    }
    .videoFootermusic__text {
        width: 79%;
    }
}